.pokem {
  position: relative;
  display: block;
  height: 70px;
  width: 70px;
  border-radius: 50%;
  background-color: #eeeeee; }
  .pokem::after, .pokem::before {
    content: "";
    border-radius: 50%; }
  .pokem::before {
    position: absolute;
    border: 35px solid transparent;
    border-top-color: #d82b2e;
    top: 0;
    left: 0;
    -webkit-animation: air-spin 2s linear infinite;
            animation: air-spin 2s linear infinite; }
  .pokem:after {
    position: absolute;
    top: 20px;
    left: 20px;
    height: 30px;
    width: 30px;
    background-color: #3a85d9; }

@-webkit-keyframes air-spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes air-spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.pacm {
  position: relative;
  display: block;
  height: 70px;
  width: 70px;
  border-radius: 50%;
  background: transparent; }
  .pacm::after, .pacm::before {
    content: "";
    display: block;
    position: absolute;
    border-radius: 50%;
    border: 35px solid #ffcc00; }
  .pacm::before {
    border-bottom-color: transparent;
    -webkit-animation: chunk-top 0.6s ease infinite;
            animation: chunk-top 0.6s ease infinite; }
  .pacm:after {
    border-right-color: transparent;
    -webkit-animation: chunk-bottom 0.6s ease infinite;
            animation: chunk-bottom 0.6s ease infinite; }

@-webkit-keyframes chunk-top {
  0%,
  100% {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg); }
  50% {
    -webkit-transform: rotate(-80deg);
            transform: rotate(-80deg); } }

@keyframes chunk-top {
  0%,
  100% {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg); }
  50% {
    -webkit-transform: rotate(-80deg);
            transform: rotate(-80deg); } }

@-webkit-keyframes chunk-bottom {
  0%,
  100% {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg); }
  50% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); } }

@keyframes chunk-bottom {
  0%,
  100% {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg); }
  50% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); } }

.look {
  position: relative;
  display: block;
  height: 70px;
  width: 70px;
  border-radius: 50%;
  background-color: #000000;
  overflow: hidden;
  border: 2px solid #000000; }
  .look::after, .look::before {
    content: "";
    display: block;
    height: 70px;
    width: 70px;
    border-radius: 50%; }
  .look::before {
    background-color: #ffffff;
    width: 18px;
    height: 18px;
    position: relative;
    top: 23px;
    left: 15px;
    -webkit-transform-origin: center;
            transform-origin: center;
    -webkit-animation: shrinkFull 3s linear infinite;
            animation: shrinkFull 3s linear infinite; }
  .look::after {
    background-color: #ffffff;
    width: 18px;
    height: 18px;
    position: relative;
    top: 5px;
    left: 34px;
    -webkit-transform-origin: center;
            transform-origin: center;
    -webkit-animation: shrinkFull 3s linear infinite;
            animation: shrinkFull 3s linear infinite; }

@-webkit-keyframes shrinkFull {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1); }
  25% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5); }
  50% {
    -webkit-transform: scale(3);
            transform: scale(3); }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1); } }

@keyframes shrinkFull {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1); }
  25% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5); }
  50% {
    -webkit-transform: scale(3);
            transform: scale(3); }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1); } }

.nuclear {
  position: relative;
  display: block;
  height: 70px;
  width: 70px;
  border-radius: 50%;
  background: #2980b9;
  -webkit-animation: ripple 2s linear infinite;
          animation: ripple 2s linear infinite; }
  .nuclear::after, .nuclear::before {
    content: "";
    display: block;
    height: 70px;
    width: 70px;
    border-radius: 50%; }
  .nuclear::before {
    background: #000;
    -webkit-animation: pulse 2s ease-out infinite;
            animation: pulse 2s ease-out infinite; }

@-webkit-keyframes ripple {
  0% {
    box-shadow: 0 0 0 0 rgba(204, 204, 204, 0.4); }
  50% {
    box-shadow: 0 0 2px 3em rgba(204, 204, 204, 0.2); }
  100% {
    box-shadow: 0 0 7em 7em rgba(204, 204, 204, 0.1); } }

@keyframes ripple {
  0% {
    box-shadow: 0 0 0 0 rgba(204, 204, 204, 0.4); }
  50% {
    box-shadow: 0 0 2px 3em rgba(204, 204, 204, 0.2); }
  100% {
    box-shadow: 0 0 7em 7em rgba(204, 204, 204, 0.1); } }

@-webkit-keyframes pulse {
  0% {
    -webkit-transform: scale(0.1);
            transform: scale(0.1); }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1); } }

@keyframes pulse {
  0% {
    -webkit-transform: scale(0.1);
            transform: scale(0.1); }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1); } }

.square-wheel {
  position: relative;
  display: block;
  height: 70px;
  width: 70px;
  background-color: #eeeeee;
  border-radius: 5px;
  -webkit-animation: spin 4s ease infinite;
          animation: spin 4s ease infinite; }
  .square-wheel::before {
    content: "";
    display: block;
    position: relative;
    top: 17px;
    left: 17px;
    height: 35px;
    width: 35px;
    background-color: #1abc9c;
    -webkit-animation: spin 2s ease infinite;
            animation: spin 2s ease infinite; }

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  50% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  50% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.twins {
  background: #cdaeda;
  position: relative;
  display: block;
  height: 70px;
  width: 70px;
  border-radius: 50%; }
  .twins::after, .twins::before {
    content: "";
    display: block;
    height: 70px;
    width: 70px;
    border-radius: 50%; }
  .twins::after {
    width: 50px;
    height: 50px;
    background: #3498db;
    position: absolute;
    top: 10px;
    left: 10px;
    -webkit-animation: move-left 2s ease infinite;
            animation: move-left 2s ease infinite; }
  .twins::before {
    width: 50px;
    height: 50px;
    background: #2c3e50;
    position: absolute;
    top: 10px;
    left: 10px;
    -webkit-animation: move-right 2s ease infinite;
            animation: move-right 2s ease infinite; }

@-webkit-keyframes move-left {
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0); }
  25% {
    -webkit-transform: translateX(20px) scale(1.2);
            transform: translateX(20px) scale(1.2); }
  50% {
    -webkit-transform: translateX(-20px);
            transform: translateX(-20px); }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0); } }

@keyframes move-left {
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0); }
  25% {
    -webkit-transform: translateX(20px) scale(1.2);
            transform: translateX(20px) scale(1.2); }
  50% {
    -webkit-transform: translateX(-20px);
            transform: translateX(-20px); }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0); } }

@-webkit-keyframes move-right {
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0); }
  25% {
    -webkit-transform: translateX(-20px) scale(1.2);
            transform: translateX(-20px) scale(1.2);
    background: #3498db; }
  50% {
    -webkit-transform: translateX(20px);
            transform: translateX(20px); }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0); } }

@keyframes move-right {
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0); }
  25% {
    -webkit-transform: translateX(-20px) scale(1.2);
            transform: translateX(-20px) scale(1.2);
    background: #3498db; }
  50% {
    -webkit-transform: translateX(20px);
            transform: translateX(20px); }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0); } }

